import React from 'react'

import {
    Container,
    Row,
    Col,
} from '../../styles/greed.style'

import {
    HeaderH1,
    Desc1,
    Desc2,
} from '../../styles/typography.style'

import Navbar from '../../components/Navbar'

const Description = () => {
    return (
        <Container>
            <Row>
                <Col>
                    <Navbar
                        desc={true}/>
                </Col>
            </Row>
            <Row mt="40">
                <Col>
                    <HeaderH1>
                        :
                    </HeaderH1>
                    <Desc1>
                         2   ,       -     ,    ,  "test"     .  1   ,     MongoDB.
                    </Desc1>
                    <Desc2 mt="40">
                          :
                    </Desc2>
                    <Desc1>
                         : React / Redux / Thunk / Styled Components.
                    </Desc1>
                    <Desc1>
                         : Node / Express / Mongoose.
                    </Desc1>
                    <Desc1>
                         : MongoDB.
                    </Desc1>
                    <Desc1>
                          : AWS ES2 - Ubunu.18.04   Docker  Nginx.
                    </Desc1>
                </Col>
            </Row>
        </Container>
    )
}

export default Description
